@import "helpers/variables";
@import "helpers/mixins";
@import "helpers/functions";
@import "vendor/libs";
@import "base/fonts";
@import "base/general";
@import "../blocks/modules";

$light-color: #fff;
$accent-color: #65e496;
$bg-light-color: #f5f6fa;
$bg-dark-color: #44444f;
$gray-color: #9191b5;

h2,
h3,
h4,
p {
  padding: 0;
  margin: 0;
}

h3 {
  font-size: 24px;
  letter-spacing: 1.2px;
}

body {
  font-family: "Roboto Slab", serif;
  font-size: 15px;
  line-height: 24px;
}

small {
  font-style: italic;
  font-size: 12px;
  letter-spacing: 0.1px;
}

.wrap {
  max-width: 1170px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

.page-main {
  background-image: url(../img/bg-main.png);
  background-repeat: no-repeat;
  background-size: cover;
  color: $light-color;
  padding-bottom: 163px;
}

.page {
  padding-top: 160px;
  padding-bottom: 160px;
  position: relative;

  &_gray {
    background-color: $bg-light-color;
  }

  &__title {
    font-size: 48px;
    text-align: center;
    margin-bottom: 25px;
    letter-spacing: 2px;
    line-height: 1.1;
  }

  &__subtitle {
    font-size: 18px;
    color: $accent-color;
    text-align: center;
    letter-spacing: 0.9px;
  }

  &__btn {
    background: linear-gradient(to top left, #65e49c, #65e4af);
    border: none;
    color: $light-color;
    text-decoration: none;
    border-radius: 3px;
    text-align: center;
    font-size: 18px;
    margin-top: 28px;
    padding: 11px 45px 15px 45px;
    letter-spacing: 0.7px;
    box-shadow: 0 -2px #adedbb inset;
  }

  &__svg {
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    min-height: 694px;
  }
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1170px;
  margin: 0 auto;
  padding: 38px 15px 166px 15px;

  &__logo {
    display: block;
    width: 137px;
    height: 28px;
    background-image: url(../img/Logo.svg);
  }

  &-list {
    list-style-type: none;
    display: flex;
    justify-content: flex-end;
    font-family: "Open Sans", serif;

    &__item {
      margin-left: 22px;
      padding-bottom: 5px;
      border-bottom: 3px solid transparent;
      transition: 0.3s;

      &:hover {
        border-bottom: 3px solid $accent-color;
      }
    }

    &__item.active {
      border-bottom: 3px solid $accent-color;
    }

    &__link {
      color: $light-color;
      text-decoration: none;
      letter-spacing: 0.5px;
    }
  }
}

.block-text {
  flex-basis: calc(93% - 500px);

  &--width {
    flex-basis: calc(93% - 520px);
  }

  &__name {
    font-family: "Open Sans", serif;
    font-size: 18px;
    letter-spacing: 0.85px;
    margin-bottom: 28px;
    margin-left: 4px;
    margin-top: 1px;
  }

  &__name_top {
    margin-top: 16px;
    margin-left: 0;
    margin-bottom: 19px;
  }

  &__title {
    font-size: 48px;
    line-height: 53px;
    font-weight: 700;
    letter-spacing: 1.8px;
    margin-bottom: 38px;
    line-height: 1.1;

    span {
      display: block;
      color: #65e496;
      font-size: 18px;
      margin-top: 23px;
      line-height: 23px;
      margin-top: 11px;
      letter-spacing: 0.9px;
      margin-left: 3px;
    }
  }

  &__description {
    font-family: "Open Sans", serif;
    font-size: 15px;
    line-height: 24px;
    margin-bottom: 24px;
    letter-spacing: 0.75px;
  }

  &__btn {
    display: block;
    background: linear-gradient(to top left, #65e49c, #65e4af);
    color: $light-color;
    text-decoration: none;
    max-width: 250px;
    border-radius: 3px;
    text-align: center;
    margin-top: 47px;
    margin-bottom: 19px;
    padding: 11px 0 15px 0;
    letter-spacing: 0.7px;
    box-shadow: 0 -2px #adedbb inset;
  }

  &__btn--main {
    box-shadow: 0 -2px #59ba88 inset;
  }

  &-item {
    margin-bottom: 55px;

    &__date {
      font-size: 15px;
      font-family: "Open Sans", serif;
      letter-spacing: 0.8px;
      margin-bottom: 4px;
    }

    &__title {
      font-size: 18px;
      letter-spacing: 0.9px;
      margin-bottom: 16px;
    }

    &__desc {
      font-size: 15px;
      font-family: "Open Sans", serif;
      letter-spacing: 0.75px;
    }

    &__btn {
      display: block;
      background: linear-gradient(to top left, #65e49c, #65e4af);
      color: $light-color;
      text-decoration: none;
      max-width: 250px;
      border-radius: 3px;
      text-align: center;
      margin-top: 47px;
      padding: 11px 0 15px 0;
      letter-spacing: 0.7px;
      box-shadow: 0 -2px #adedbb inset;
    }
  }
}

.block-book {
  display: flex;
  justify-content: flex-start;

  &-demo {
    display: block;
    position: relative;
    width: 447px;
    height: 643px;
    background-image: url(../img/book.png);
    background-repeat: no-repeat;
    flex-basis: 500px;

    &__title {
      font-size: 36px;
      text-transform: uppercase;
      letter-spacing: 1.4px;
      position: absolute;
      left: 131px;
      top: 118px;

      span {
        font-family: "Open Sans", serif;
        font-size: 14px;
        color: $accent-color;
        display: block;
        text-transform: initial;
        margin-top: 10px;
        letter-spacing: 0.7px;
      }
    }

    &__autor {
      font-family: "Open Sans", serif;
      font-size: 14px;
      color: $accent-color;
      position: absolute;
      letter-spacing: 0.7px;
      bottom: 52px;
      left: 87px;
    }
  }
}

.block-author {
  display: flex;
  justify-content: space-between;
}

.block-ava {
  position: relative;

  &__svg {
    position: absolute;
    z-index: -1;
    right: 0;
  }

  &__svg--left {
    right: inherit;
    left: 0;
  }

  &__img {
    margin-top: 47px;
    margin-right: 44px;
  }

  &__img--left {
    margin-top: 49px;
    margin-left: 51px;
    margin-right: 0;
  }
}

.block-counter {
  display: flex;
  justify-content: space-around;
  margin-top: 152px;

  p {
    font-family: "Open Sans", serif;
    text-align: center;
    font-size: 18px;

    span {
      font-family: "Roboto Slab", serif;
      display: block;
      font-weight: 100;
      font-size: 80px;
      color: $accent-color;
      margin-bottom: 45px;
    }
  }
}

.block-about {
  display: flex;
  justify-content: space-between;
  margin-top: 182px;

  h3 {
    margin-bottom: 20px;
  }

  &-text {
    flex-basis: calc(100% - 690px);

    &__desc {
      font-family: "Open Sans", serif;
      letter-spacing: 0.6px;
    }
  }

  &-table {
    flex-basis: 585px;

    &__list {
      margin: 25px 0 0 0;
      padding: 0;
      counter-reset: list;
      column-count: 2;
      column-gap: 19px;

      &>li {
        position: relative;
        padding-left: 36px;
        counter-increment: list;
        list-style-type: none;
        margin-bottom: 8px;
        letter-spacing: 0.7px;

        &::before {
          content: counter(list) ". ";
        }

        &::after {
          content: "+";
          font-family: "Open Sans", serif;
          font-size: 15px;
          line-height: 22px;
          position: absolute;
          left: 0;
          top: 1px;
          background-color: #5fc9b3;
          color: $light-color;
          padding: 0px 7px;
        }
      }
    }

    &__list_next {
      counter-reset: list 10;
      position: relative;

      &::before {
        content: "";
        width: 100px;
        height: 2px;
        background-color: #78c5b2;
        display: block;
        position: absolute;
        top: -16px;
      }
    }

    &__btn {
      background-color: $accent-color;
      border: none;
      color: $light-color;
      text-decoration: none;
      border-radius: 3px;
      text-align: center;
      font-size: 18px;
      margin-top: 28px;
      padding: 11px 45px 15px 45px;
      letter-spacing: 0.7px;
      margin-bottom: 19px;
      box-shadow: 0 -2px #adedbb inset;

      &:focus {
        outline: none;
      }
    }
  }

  &-social {
    display: flex;
    align-items: center;
    margin-top: 14px;

    &__title {
      font-family: "Roboto Slab", serif;
      font-size: 18px;
      font-weight: 700;
      letter-spacing: 0.9px;
    }

    &__list {
      list-style-type: none;
      padding-left: 23px;

      &>li {
        display: inline-block;
        vertical-align: middle;
        font-size: 18px;
        margin-right: 12px;

        &>a {
          color: $accent-color;
        }
      }
    }
  }
}

.block-product {
  display: flex;
  justify-content: space-around;
  margin-top: 85px;

  &-item {
    text-align: center;
  }

  &-demo {
    display: block;
    margin: 0 auto 35px;
    position: relative;
    width: 269px;
    height: 409px;
    background-image: url(../img/book.png);
    background-repeat: no-repeat;
    flex-basis: 500px;

    &__title {
      font-size: 24px;
      color: $light-color;
      text-transform: uppercase;
      letter-spacing: 0.9px;
      position: absolute;
      left: 71px;
      top: 71px;

      span {
        font-family: "Open Sans", serif;
        font-size: 12px;
        color: $accent-color;
        display: block;
        text-transform: initial;
        margin-top: 7px;
        letter-spacing: 0.6px;
      }
    }

    &__autor {
      font-family: "Open Sans", serif;
      font-size: 12px;
      color: $accent-color;
      position: absolute;
      letter-spacing: 0.6px;
      bottom: 27px;
      left: 82px;
    }
  }

  &-text {
    h3 {
      margin-bottom: 17px;
    }

    p {
      font-size: 15px;
      max-width: 316px;
      letter-spacing: 0.7px;
      margin-bottom: 17px;
    }

    a {
      font-size: 15px;
      color: $accent-color;
      text-transform: uppercase;
      text-decoration: none;
      letter-spacing: 0.5px;
      border-bottom: 3px solid transparent;
      padding-bottom: 5px;
      transition: 0.3s;

      &:hover {
        border-bottom: 3px solid $accent-color;
      }
    }
  }

  &+.page__btn {
    display: block;
    margin: 84px auto 0;
    padding: 13px 0;
    max-width: 227px;
  }
}

.block-blog {
  display: flex;
  justify-content: space-between;
  margin-top: 85px;

  &-item {
    flex-basis: 32%;
  }

  h4 {
    font-size: 18px;
    letter-spacing: 0.9px;
    margin-bottom: 4px;
  }

  &-img {
    width: 100%;
    height: 224px;
    overflow: hidden;
    position: relative;
    margin-bottom: 15px;

    img {
      width: 100%;
      height: 224px;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      object-fit: cover;
    }
  }

  &-info {
    font-size: 15px;
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    letter-spacing: 0.8px;
    margin-bottom: 12px;

    a {
      color: $accent-color;
      text-decoration: none;
    }
  }

  &__desc {
    font-size: 15px;
    font-family: "Open Sans", serif;
    letter-spacing: 0.7px;
  }

  &+.page__btn {
    display: block;
    margin: 84px auto 0;
    padding: 13px 0;
    max-width: 227px;
  }
}

.block-letter {
  display: flex;
  justify-content: space-between;

  &-item {
    flex-basis: 49%;
  }

  &__title {
    font-size: 48px;
    margin-bottom: 25px;
    letter-spacing: 1.9px;
    margin-top: 8px;
    line-height: 1.1;
  }

  &__text {
    font-size: 15px;
    font-family: "Open Sans", serif;
    letter-spacing: 0.75px;
  }

  &__input {
    width: 100%;
    box-sizing: border-box;
    font-family: "Open Sans", serif;
    background-color: $bg-dark-color;
    border: none;
    color: $gray-color;
    padding: 15px 27px;
    border-radius: 3px;
    letter-spacing: 0.6px;

    &:focus {
      outline: none;
    }
  }

  &__btn {
    background-color: $accent-color;
    border: none;
    color: $light-color;
    text-decoration: none;
    border-radius: 3px;
    text-align: center;
    font-size: 18px;
    margin-top: 21px;
    padding: 11px 0 15px 0;
    letter-spacing: 0.7px;
    margin-bottom: 19px;
    box-shadow: 0 -2px #adedbb inset;
    display: block;
    width: 100%;

    &:focus {
      outline: none;
    }
  }
}

.footer {
  background-color: $bg-dark-color;
  color: $gray-color;
  padding: 80px 0;

  .block-footer {
    display: flex;
    justify-content: space-between;

    &>div {
      flex-basis: 30%;
    }

    &__logo {
      display: block;
      width: 137px;
      height: 28px;
      background-image: url(../img/Logo.svg);
    }

    &__title {
      font-size: 18px;
      color: $accent-color;
    }

    &__list {
      list-style-type: none;
      padding-left: 0;
    }

    &__social {
      display: flex;
      list-style-type: none;
      padding-left: 0;

      &>li {
        margin-right: 10px;
        font-size: 15px;

        &>a {
          color: $accent-color;
          transition: 0.3s;

          &:hover {
            color: $bg-light-color;
          }
        }
      }
    }
  }
}

.block-carousel {
  text-align: center;

  h3 {
    color: $accent-color;
    margin-bottom: 4px;
  }

  &__desc {
    color: $gray-color;
    font-family: "Open Sans", serif;
    max-width: 760px;
    margin: 0 auto;
    line-height: 27px;
    letter-spacing: 0.75px;
  }

  &__expert {
    margin-bottom: 32px;
    color: $gray-color;
  }

  &-ava {
    position: relative;
    width: 152px;
    height: 152px;
    display: block;
    margin: 0 auto 27px;
  }

  &-bq {
    position: absolute;
    display: block;
    background-image: url(../img/bq.png);
    background-repeat: no-repeat;
    width: 48px;
    height: 42px;
    top: 0;
    right: -15px;
  }

  &-thumb {
    display: block;
    margin: 0 auto 0;
    position: relative;
    overflow: hidden;
    width: 152px;
    height: 152px;
    border-radius: 100%;

    img {
      width: 152px;
      height: 152px;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      object-fit: cover;
    }
  }
}

.slick-dots {
  display: flex;
  justify-content: center;
  list-style-type: none;

  &>li {
    margin: 0 5px;

    &>button {
      text-indent: -9999px;
      width: 8px;
      height: 8px;
      border: none;
      background-color: $accent-color;
      border-radius: 100%;
      line-height: 0;
      font-size: 0;
      cursor: pointer;
      padding: 5px;

      &:focus {
        outline: none;
      }
    }
  }

  &>li.slick-active {
    &>button {
      width: 37px;
      border-radius: 10px;
    }
  }
}





@media(max-width:1020px) {
  .page {
    padding-top: 120px;
    padding-bottom: 120px;

    &__svg {
      display: none;
    }

    &-carousel {
      background-color: $bg-dark-color;
    }
  }

  .header {
    padding: 38px 15px 100px 15px;
  }

  .block-text {
    //flex-basis: call(93% - 315px);
    flex-basis: call(93% - 10%);

    &__btn--main {
      margin: 47px auto 19px;
    }
  }

  .block-book {
    flex-direction: column-reverse;
    text-align: center;

    &-demo {
      //flex-basis: 100%;
      flex-basis: 600px;
      margin: 56px auto 0;
    }
  }

  .block-ava {
    position: relative;

    &__svg {
      width: 300px;
      height: 412px;
    }

    &__img {
      width: 300px;
    }
  }

  .block-about {
    max-width: 750px;
    margin: 82px auto 0;
    flex-direction: column;

    &-text {
      flex-basis: 100%;
    }

    &-table {
      flex-basis: 100%;
      margin-top: 40px;
    }
  }

  .block-product {
    flex-direction: column;

    &-text {
      p {
        margin: 0 auto 17px;
      }

      a {
        display: block;
        margin: 0 auto 50px;
        max-width: 120px;
      }
    }
  }

  .block-letter {
    flex-direction: column;
    text-align: center;

    &-item {
      flex-basis: 100%;
    }

    &__text {
      margin-bottom: 40px;
    }

    &__btn {
      max-width: 500px;
      margin: 21px auto 0;
    }

    &__input {
      max-width: 500px;
    }
  }
}

@media(max-width:900px) {
  .header {
    flex-direction: column;
  }

  .block-blog {
    flex-direction: column;
    max-width: 600px;
    margin: 85px auto 0;

    &-item {
      margin-bottom: 40px;
    }
  }
}

@media(max-width:750px) {
  .block-counter {
    flex-direction: column;

    p {
      margin-bottom: 80px;
    }
  }

  .block-author {
    flex-direction: column-reverse;
    text-align: center;
  }

  .block-ava {
    position: relative;
    max-width: 356px;
    margin: 0 auto 80px;
  }

  .block-text {
    &__btn {
      margin: 47px auto;

    }

    &-item {
      &__btn {
        margin: 0px 210px;
      }
    }
  }
}

@media(max-width:630px) {
  .block-about {
    &-table {
      &__list {
        column-count: 1;
      }
    }
  }

  .block-text {
    flex-direction: column;
    text-align: center;
  }

  .block-text {
    &-item {
      &__btn {
        padding: 8px 110px;
        margin: 0px 100px;
        text-align: center;
      }
    }
  }
}

@media(max-width:500px) {
  .block-book {
    &-demo {
      display: none;
    }
  }

  .block-letter {
    &__title {
      font-size: 30px;
    }
  }

  .page {
    padding-top: 50px;
    padding-bottom: 50px;

    &__title {
      font-size: 30px;
      margin-bottom: 15px;
    }

    &__subtitle {
      font-size: 16px;
    }
  }

  .block-text {
    &__title {
      font-size: 30px;
    }

    span {
      font-size: 16px;
    }
  }

  .block-counter {
    p {
      margin-bottom: 45px;

      span {
        font-size: 60px;
        margin-bottom: 29px;
      }
    }
  }

  .footer {
    background-color: $bg-dark-color;
    color: $gray-color;
    padding: 80px 0;

    .block-footer {
      flex-direction: column;
      text-align: center;

      &__logo {
        margin-left: 100px;
      }

      &__social {
        margin-left: 100px;
      }

    }
  }

  .block-text {
    &-item {
      &__btn {
        padding: 8px 110px;
        margin: 0px 15px;
        text-align: center;
      }
    }
  }
}